<template lang='pug'>
  v-container(fluid app grid-list-md).bg-simple
    v-subheader.headline –ü–∞–Ω–µ–ª—å –∞–¥–º—ñ–Ω—ñ—Å—Ç—Ä–∞—Ç–æ—Ä–∞ {{ currentGroup.name }}
    div(v-if='showBlank === "createUser" || showBlank === ""')
      v-subheader.headline –°—Ç–≤–æ—Ä–∏—Ç–∏ —Ä–æ–ª—å –≤ –≥—Ä—É–ø—ñ
      v-layout(row wrap)
        v-flex(xs12 lg6)
          v-card.px-3.py-3.mt-2
            v-form(ref='adminForm' v-model='valid')

              v-text-field(
                type='text'
                v-model='user.name'
                label='–ü–Ü–ë –≤–ª–∞—Å–Ω–∏–∫–∞'
                placeholder='–§–∞–º—ñ–ª—ñ—è, –ò º–º—è, –ü–æ –±–∞—Ç—å–∫–æ–≤—ñ'
                required
                :rules='rules')

              v-text-field(
                type='text'
                v-model='user.role'
                required
                :rules='rules'
                label='–ù–∞–∑–≤–∞ —Ä–æ–ª–∏ –∫–æ—Ä—Å—Ç—É–≤–∞—á–∞'
                placeholder='–†–æ–ª—å')

              v-text-field(
                  type='number'
                  v-model='user.cerTime'
                  label='–°—Ç—Ä–æ–∫ –¥—ñ—ó —Å–µ—Ä—Ç–∏—Ñ–∏–∫–∞—Ç–∞, –¥—ñ–±'
                  placeholder='0 - –Ω–µ–æ–±–º–µ–∂–µ–Ω–æ'
                  :rules='rules'
                  required)

              v-text-field(
                  type='email'
                  v-model='user.email'
                  required
                  label='–í–≤–µ–¥—ñ—Ç—å –ïmail –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á–∞'
                  description='–£–≤–∞–≥–∞! –ù–∞ –Ω—å–æ–≥–æ –≤—ñ–¥–ø—Ä–∞–≤–∏—Ç—å—Å—è –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è —Ç–∞ –∫–ª—é—á—ñ —Ü—Ñ—Ä–æ–≤–æ–≥–æ –ø—ñ–¥–ø–∏—Å—É!'
                  :rules='rules'
                  placeholder='example@mail.ua')

              v-btn(@click.prevent='createUser' color='primary' :disabled='!valid') –°—Ç–≤–æ—Ä–∏—Ç–∏
        v-flex(xs12 lg6)
          v-card.px-3.py-3.mt-2
            v-subheader.headline –ö–æ—Ä–∏—Å—Ç—É–≤–∞—á—ñ –≥—Ä—É–ø–∏: {{ currentGroup.name }}
            div.users-list
              template(v-for='—ÅurrUser in currentGroup.users')
                v-card.px-3
                  v-subheader {{ —ÅurrUser.author }}
                  p.mb-0 –†–æ–ª—å: {{ —ÅurrUser.role }}
                  p.mb-0 –ó–∞—Ä–µ—î—Å—Ç—Ä–æ–≤–∞–Ω: {{ toDateString(+—ÅurrUser.dateRegistration) }}
                v-divider

    div(v-if='showBlank === "usersStats"')
      v-subheader.headline –°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞ –≥—Ä—É–ø–∏
      users-stat

    v-snackbar(v-model='showAlert' top right)
      | {{ info }}
      v-btn(flat @click.native='showAlert = false' color='pink') –ó–∞–∫—Ä–∏—Ç–∏
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import toDateString from '../modulesJs/toDateString'
export default {
  data () {
    return {
      user: {
        role: '',
        email: '',
        login: '',
        name: '',
        certTime: 0
      },
      info: '',
      showAlert: false,
      valid: false,
      rules: [ v => !!v || '–ü–æ–ª–µ –æ–±–æ–≤ º—è–∑–∫–æ–≤–µ!' ],
      showBlank: ''
    }
  },
  computed: {
    ...mapGetters('groupsStore', ['currentGroup']),
    ...mapGetters('usersStore', ['currentUser'])
  },
  methods: {
    ...mapActions('groupsStore', ['createNewUser', 'getCurrentGroup']),
    toDateString,
    createUser (e) {
      this.createNewUser({ ...this.user, group: this.currentGroup._id })
        .then(response => {
          this.$refs.adminForm.reset()
          this.info = response.message
          this.showAlert = true
        })
        .catch(e => {
          this.info = e.message
          this.showAlert = true
        })
    }
  },
  watch: {
    '$route': function () {
      this.showBlank = this.$route.params.blank
    }
  },
  components: {
    usersStat: require('./usersStat')
  }
}
</script>
<style lang='sass' scoped>
.admin
  padding: 40px 0
.users-list
  height: 297px
  overflow-y: scroll
</style>



