<template lang='pug'>
  div
    v-navigation-drawer(
      fixed
      app
      v-model='sidebar'
      v-if='show !== "adminBar" && show !== "superadminBar"')
      v-list
        v-list-tile
          v-btn(to='/edms' flat) –ù–æ–≤—ñ –¥–æ–∫—É–º–µ–Ω—Ç–∏
        v-list-tile
          v-btn(to='/edms/addNew' flat) –î–æ–¥–∞—Ç–∏ –¥–æ–∫—É–º–µ–Ω—Ç
        v-list-tile
          v-btn(to='/edms/myDocuments' flat) –ú–æ—ñ –¥–æ–∫—É–º–µ–Ω—Ç–∏
        v-list-tile
          v-btn(to='/edms/archive' flat) –ê—Ä—Ö—ñ–≤
    v-navigation-drawer(
      fixed
      app
      v-model='sidebar'
      v-if='show === "superadminBar"')
      v-list
        v-list-tile
          v-btn(to='/edms/superAdmin/createGroup' flat) –°—Ç–≤–æ—Ä–∏—Ç–∏ –Ω–æ–≤—É –≥—Ä—É–ø—É
        v-list-tile
          v-btn(to='/edms/superAdmin/createAdmin' flat) –°—Ç–≤–æ—Ä–∏—Ç–∏ –∞–¥–º—ñ–Ω—ñ—Å—Ç—Ä–∞—Ç–æ—Ä–∞
        v-list-tile
          v-btn(to='/edms/superAdmin/groups' flat) –ì—Ä—É–ø–∏
    v-navigation-drawer(
      fixed
      app
      v-model='sidebar'
      v-if='show === "adminBar"')
      v-list
        v-list-tile
          v-btn(to='/edms/admin/createUser' flat) –°—Ç–≤–æ—Ä–∏—Ç–∏ –Ω–æ–≤–æ–≥–æ –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á–∞
        v-list-tile
          v-btn(to='/edms/admin/usersStats' flat) –°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞

    v-toolbar(app dense)
      v-toolbar-side-icon(v-if='show !== "indexBar"' @click='sidebar = !sidebar')
      v-toolbar-title(v-if='show !== "indexBar"') {{ currentUser.author }}
      v-toolbar-title(v-if='show === "indexBar"') EDMS
      v-spacer
      v-btn(@click='logOut' color='secondary' small v-if='show !== "indexBar"') –í–∏–π—Ç–∏
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
export default {
  data () {
    return {
      // get id of current document
      route: this.$router.currentRoute.fullPath,
      sidebar: false
    }
  },
  computed: {
    ...mapGetters('usersStore', ['currentUser']),
    show () {
      const route = this.$route.fullPath
      const role = this.currentUser.role

      if (route === '/' || route === '/auth' || route === '/registration') {
        return 'indexBar'
      }
      if (role === 'Admin') return 'adminBar'
      if (role === 'superAdmin') return 'superadminBar'
      return 'userBar'
    }
  },
  methods: {
    ...mapActions(['logout']),
    logOut () {
      this.sidebar = false
      this.$router.push('/')
      this.logout()
    }
  }
}
</script>
<style lang="sass" scoped>
.logo
    display: block
    height: 35px
    width: auto
</style>



